.loginLogo,
.regLogo {
	max-width: 25%;
	justify-content: center;
	text-align: center;
}

.loginText,
.regText {
	display: block;
	margin: auto;
	margin-top: 20px;
	text-align: center;
	width: 20%;
}

.loginButton,
.regButton {
	display: block;
	margin: auto;
	margin-top: 20px;
	padding-top: 10px;
	background-color: #2e3138;
	border: none;
	color: #56b19c;
	padding: 7px 2px;
	text-align: center;
	font-size: larger;
	border-radius: 12px;
	width: 15%;
}

.loginButton:hover,
.regButton:hover {
	box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
		0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.logToReg,
.regToLog {
	color: #56b19c;
	background: none;
	border: none;
}

.dashTitle {
	display: flex;
	margin-left: 5%;
}
.dash {
	display: flex;
	margin-left: 5%;
}

.dashNote {
	margin-left: 60%;
	width: 250%;
  	height: 100%;
  	padding: 12px 20px;
  	box-sizing: border-box;
  	border: 2px solid #ccc;
  	border-radius: 4px;
  	background-color: #f8f8f8;
  	resize: none;
}

.dashButton, .dashButton1 {
	display: block;
	margin: auto;
	margin-top: 15px;
	padding-top: 10px;

	background-color: #2e3138;
	border: none;
	color: #56b19c;
	padding: 22px 40px;
	text-align: center;


	border-radius: 12px;

	width: 100%;
}
.dashButton1 {
	margin-left: 140%;
}

.dashButton:hover, .dashButton1:hover {
	box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
		0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.errorText {
	color: #fc6161;
}

.graph {
	display: grid;
	columns: 1;
}