*{
    box-sizing: border-box;
}

body{
    margin: 0;
}

.nav{
    background-color: #333;
    color: #56B19C;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 2rem;
    padding: 0 1rem;
    font-size: 1rem;
}

.site-title {
    font-size: 2rem;
}

.nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
}

.nav a {
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;
}

.nav li.active {
    background-color: #856A5d;
    color: #2E3138;
    font: bold;
}

.nav li:hover {
    background-color: #484a50;
    color: #56B19C;
}